<template>
    <div class="circle">
         <Header></Header>
         <ul class="pd40">
           <li v-for="(it,i) in list" :key="i">
             <img :src="imgUrl+it.url" alt="" class="twoCode">
             <div class="text">
               <span>{{it.title}}：</span>
               <span class="blue">“{{it.copy_content}}”</span>
              </div>
             <p>{{it.intro}}</p>
             <div class="copyBtn" @click="c(it)" v-clipboard:copy="it.copy_content" v-clipboard:success="onCopy">
               复制账号
             </div>
           </li>
         </ul>
      <!-- 点击提示 -->
      <div class="apply_hint ls1" v-show="apply_hint" @click="apply_hint=false">
        <div class="hint_box bgfff pd40" @click.stop="">
          <img v-if="cur_copy.slug==='wechat_public'" src="../../images/wei-xin.png" alt="" class="wxPic">
          <img v-if="cur_copy.slug==='wechat_person'" src="../../images/wei-xin.png" alt="" class="wxPic">
          <img v-if="cur_copy.slug==='qq'" src="../../images/QQ@2x.png" alt="" class="wxPic">
          <div class="hint_info" v-if="cur_copy.slug==='wechat_public'">已复制{{cur_copy.title}}：“{{curCopyWx.text}}” </div>
          <div class="hint_info" v-if="cur_copy.slug==='wechat_person'">已复制{{cur_copy.title}}：“{{curCopyWx.text}}” </div>
          <div class="hint_info" v-if="cur_copy.slug==='qq'">已复制{{cur_copy.title}}：“{{curCopyWx.text}}” </div>
          <a href="javascript:;" v-if="cur_copy.slug==='wechat_public'" @click="goWx" class="fwb">立即去微信</a>
          <a href="javascript:;" v-if="cur_copy.slug==='wechat_person'" @click="goWx" class="fwb">立即去微信</a>
          <a href="javascript:;" v-if="cur_copy.slug==='qq'" @click="goWx" class="fwb">立即去QQ</a>
          <div class="close_hint">
            <p class="bgfff"></p>
            <img src="../../images/tui-chu.png" alt="" @click="apply_hint=false">
          </div>
        </div>
      </div>
      <!-- 提示语 -->
      <transition name="fade" v-show="apply_hint">
        <div class="toast_box">
          <p class="toast" v-show="show">
            <span>已复制账号到粘贴板</span>
          </p>
        </div>
      </transition>
    </div>
</template>
<script>
import Header from '@/components/Header'
import toast from '@/components/toast'
export default {
  components: {
    Header,
    toast
  },
  data () {
    return {
      list: [],
      apply_hint: false, // 申请提示弹窗
      collect: false, // 收藏
      curCopyWx: '', // 当前复制的微信
      cur_copy: {},
      show: false,
      message: '',
      type: 0
    }
  },
  created () {
    this.getCicleData()
  },
  methods: {
    onCopy (e) {
      this.curCopyWx = e
      this.apply_hint = true
      // this.$toast('已复制账号到粘贴板', 2000, 2)
    },
    c (item) {
      // console.log(item)
      this.cur_copy = item
      this.show = !this.show
      setTimeout(()=>{
        this.show = false
      },600)
    },
    // 获取圈子数据
    getCicleData () {
      this.fetch.circle({},(res => {
        if (res.code===200){
          this.list = res.data
        }else if(res.code===2004){
           this.$toast('暂无圈子', 2000, 1)
        }
      }))
    },
    goApp(){
      if (this.cur_copy.slug === 'wechat_public') {

      } else if (this.cur_copy.slug === 'qq') {

      }
    },
    goWx (url) {
      // setTimeout(function () {
      //   if (!hasApp) {
      //   // 没有安装微信
      //   var r = confirm("您没有安装微信，请先安装微信!")
      //   if (r === true) {
      //     location.href="http://weixin.qq.com/"
      //   }
      //   } else {
      //   // 安装微信
      //   }
      //   document.body.removeChild(ifr)
      // }, 2000)
      let u = navigator.userAgent;
      if (u.indexOf('MicroMessenger') > -1) {
        confirm("请在系统浏览器打开")
        return
      }
      if (this.cur_copy.slug === 'wechat_public') {
        if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
          // ios
          console.log('ios wx')
          window.location = 'itms-apps://itunes.apple.com/app/414478124';
        } else if (u.indexOf('Android') > -1 || u.indexOf('Adr') > -1) {
          // Android 
          console.log('Android wx')
          var ifr = document.createElement("iframe");
          ifr.setAttribute('src', 'weixin://')
          ifr.setAttribute('style', 'display:none')
          document.body.appendChild(ifr)
        }
      } else if (this.cur_copy.slug === 'qq') {
        if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
          // ios
          console.log('ios qq')
          window.location = `mqq://im/chat?chat_type=wpa&uin=${this.cur_copy.copy_content}&version=1&src_type=web`
        } else if (u.indexOf('Android') > -1 || u.indexOf('Adr') > -1) {
          // Android
          console.log('Android qq')
          var ifr = document.createElement("iframe");
          ifr.setAttribute('src', `mqq://im/chat?chat_type=wpa&uin=${this.cur_copy.copy_content}&version=1&src_type=web`)
          ifr.setAttribute('style', 'display:none')
          document.body.appendChild(ifr)
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
// 圈子
.circle{
  .apply_hint .hint_info{
    margin: .4rem 0 .6rem 0;
    color: #101d37;
     font-size: .26rem;
    text-align: center;
  }
}
.blue{
  color: #2e6eec;
}
ul{
  & li{
    background-color: #fff;
    box-shadow: 0 .04rem .16rem 0 rgba(0, 0, 0, 0.1);
    border-radius: .1rem;
    padding: .8rem .4rem .4rem .4rem;
    margin-bottom: .4rem;
    & .twoCode{
      width: 2.64rem;
      display: block;
      margin: 0 auto;
      text-align: center;
    }
    & .text{
      text-align: center;
      font-size: .3rem;
      letter-spacing: .01rem;
      font-weight: 700;
      margin: .8rem 0 .38rem 0;
    }
    & p{
      line-height: .4rem;
      font-size: .26rem;
      letter-spacing: .01rem;
	    color: #101d37;
    }
    & .copyBtn{
      width: 3.2rem;
      height: .9rem;
      background-image: linear-gradient(-90deg,#2e6eec 0%,#6699ff 100%),linear-gradient(#9fa5b3,#9fa5b3);
      background-blend-mode: normal,normal;
      border-radius: .1rem;
      font-size: .3rem;
      color: #fff;
      line-height: .9rem;
      letter-spacing: .01rem;
      margin: .8rem auto 0;
      text-align: center;
    }
  }
}
.wxPic{
  width: .88rem;
  display: block;
  margin: 0 auto;
  text-align: center;
  margin-top: .4rem;
}
// 提示框
.toast_box{
  position: fixed;
  top: 80%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toast {
  box-sizing: border-box;
  padding: .2rem;
  line-height: .4rem;
  max-width: 6rem;
  min-width: 2rem;
  font-size: .26rem;
  color: #222;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: .1rem;
  z-index: 999;
  letter-spacing: .01rem;
  span{
    vertical-align: middle;
  }
}
</style>



